/* QuizList.css */
.quiz-list {
    margin-top: 20px;
  }
  
  .quiz-list ul {
    list-style: none;
    padding: 0;
  }
  
  .quiz-list li {
    background-color: #f9f9f9;
    padding: 10px;
    margin: 10px 0;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .error-message {
    color: red;
    font-weight: bold;
  }
  